import React, { useEffect, useState } from 'react';
import {
  View,
  Text,
  StyleSheet,
  ScrollView,
  Image,
  TouchableOpacity,
} from 'react-native';
import LetterAvatar from './LetterAvatar';
import webSocketService from '../services/WebSocket';
import ProfileModal from './ProfileModal';

interface OnlineUser {
  id: number;
  username: string;
  name: string;
  avatar: string;
}

const OnlineUsersBar: React.FC = () => {
  const [onlineUsers, setOnlineUsers] = useState<OnlineUser[]>([]);
  const [selectedUser, setSelectedUser] = useState<string | null>(null);

  useEffect(() => {
    const handler = (data: any) => {
      if (data.type === 'social' && data.action === 'get_online_users') {
        setOnlineUsers(data.users || []);
      }
    };

    webSocketService.addMessageHandler('social', handler);
    webSocketService.send({
      type: 'social',
      action: 'get_online_users'
    });

    return () => {
      webSocketService.removeMessageHandler('social');
    };
  }, []);

  if (onlineUsers.length === 0) return null;

  return (
    <View style={styles.container}>
      <View style={styles.header}>
        <Text style={styles.title}>  </Text>
      </View>
      
      <ScrollView 
        horizontal 
        showsHorizontalScrollIndicator={false}
        style={styles.scrollView}
      >
        {onlineUsers.map((user) => (
          <TouchableOpacity 
            key={user.id}
            style={styles.userContainer}
            onPress={() => setSelectedUser(user.username)}
          >
            <View style={styles.avatarContainer}>
              {user.avatar && user.avatar !== 'None' ? (
                <Image
                  source={{ uri: `https://elemsocial.com/Content/Avatars/${user.avatar}` }}
                  style={styles.avatar}
                />
              ) : (
                <LetterAvatar
                  name={user.name || user.username}
                  size={50}
                />
              )}
              <View style={styles.onlineBadge} />
            </View>
            <Text style={styles.userName} numberOfLines={1}>
              {user.name || user.username}
            </Text>
          </TouchableOpacity>
        ))}
      </ScrollView>

      <ProfileModal
        visible={!!selectedUser}
        username={selectedUser || ''}
        onClose={() => setSelectedUser(null)}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    backgroundColor: '#fff',
    paddingVertical: 10,
    borderBottomWidth: 1,
    borderBottomColor: '#eee',
  },
  header: {
    paddingHorizontal: 15,
    marginBottom: 10,
  },
  title: {
    fontSize: 16,
    fontWeight: '600',
    color: '#000',
  },
  scrollView: {
    paddingHorizontal: 10,
  },
  userContainer: {
    alignItems: 'center',
    marginHorizontal: 5,
    width: 60,
  },
  avatarContainer: {
    width: 50,
    height: 50,
    marginBottom: 5,
    position: 'relative',
  },
  avatar: {
    width: '100%',
    height: '100%',
    borderRadius: 25,
    backgroundColor: '#f0f0f0',
  },
  onlineBadge: {
    position: 'absolute',
    bottom: -2,
    right: -2,
    width: 12,
    height: 12,
    borderRadius: 6,
    backgroundColor: '#4CAF50',
    borderWidth: 2,
    borderColor: '#fff',
    zIndex: 1,
  },
  userName: {
    fontSize: 12,
    color: '#666',
    textAlign: 'center',
    width: '100%',
  },
});

export default OnlineUsersBar; 